
.content {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
}
.content ul {
    position: relative;
    bottom: auto;
    overflow: auto;
    height: 100%;
    text-align: left;
    margin-left: 10px;
}
.content .messages {
    overflow: hidden;
}
.messages {
  height: 75%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
}
.countFights {
  height: 20px;
  width: 20px;
  border-radius: 50px;
  background-color: lightblue;
  text-align: center;
}
.countInjuries {
  height: 20px;
  width: 20px;
  border-radius: 50px;
  background-color: darkred;
  color: white;
  text-align: center;
}
.rotate90 {
  transform: rotate(270deg);
}
.lastCard {
  height: 23vh;
}
.cardStyles {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  background-color: rgba(100, 100, 100, 0.7);
  padding: 20px;
  width: 8vw;
  height: 16vh;
  border-radius: 25px;
}
.img-opp {
  height: 70%;
  /*width:250px;*/
}
.flex-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 11vh;
  width: 100%;
}
.bgpic {
  background-image: url(https://northendorg.files.wordpress.com/2016/09/freakalley.jpg?w=4000&h=&crop=1);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: fixed;
  height: 100vh;
  /*overflow-y: scroll;*/
  padding-top: 5rem;
}
.bggame {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  /*overflow-y: hidden;*/
  z-index: 1;
}
.typing {
  position: absolute;
  bottom: 0;
  width: 75%;
  height: 25%;
  text-align: left;
  margin-left: 10px;
}
.textbox {
  background-color: white;
  width: 20%;
  height: 50%;
  position: fixed;
  bottom: 0;
  right: 0;
  /*margin-bottom: 81px;*/
}

/*.hand {
/*display: inline-flex;*/
/*transition: transform 500ms ease-out;*/
/*transform: scale(1) translateY(0);*/
/*}*/
.hand:hover {
  transform: scale(1.25) translateY(0);
  padding-bottom: 100px;
  /*margin-left: 100px;
margin-right: 100px;*/
  z-index: 1;
}
.flex-hand {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: end;
      align-items: flex-end;
  bottom: 0;
  position: fixed;
  width: 60%;
  margin-left: 20%;
  margin-right: 20%;
  /*overflow-x: hidden;*/
  /*overflow-y: visible;*/
  padding-left: 50px;
}
.flex-injury {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  -ms-flex-direction: column;
      flex-direction: column;
  position: fixed;
  bottom: 0;
}
.card {
  height: 19vh;
  z-index: 0;
  margin-left: -50px;
  transition: transform 500ms ease-out;
  transform: scale(1) translateY(0);
}
.card-valid {
  -webkit-filter: drop-shadow(0px 0px 8px rgba(0, 0, 0, 0.8));
  box-shadow: 0px 0px 20px 10px #ff8;
}
.injury {
  height: 19vh;
  z-index: 0;
  /*margin-bottom: -50px;*/
  transition: transform 500ms ease-out;
  transform: scale(1) translateY(0);
}
.injury:hover {
  z-index: 1;
}
.injuryHand {
  display: inline;
  transition: transform 500ms ease-out;
  transform: scale(1) translateY(0);
}
.card:hover {
  /*transform: scale(1.25) translateY(-200px);*/
  /*margin-left: 100px;
margin-right: 100px;*/
  z-index: 1;
}
.deck-fight {
  border-radius: 25px;
  height: 11vh;
  margin: 10px;
  -webkit-filter: drop-shadow(0px 0px 0px rgba(255, 255, 255, 0.8));
  transition: all 0.5s linear;
}
.deck-fight-valid {
  -webkit-filter: drop-shadow(0px 0px 8px rgba(0, 231, 255, 0.8));
  box-shadow: 0px 0px 20px 10px #0ff;
}
.deck-fight-valid:hover {
  -webkit-filter: drop-shadow(0px 0px 8px rgba(0, 231, 255, 0.8));
  box-shadow: 0px 0px 35px 15px #7ff;
}
.deck-injury {
  border-radius: 25px;
  height: 11vh;
  margin: 10px;
  -webkit-filter: drop-shadow(0px 0px 0px rgba(255, 255, 255, 0.8));
  transition: all 0.5s linear;
}
.deck-injury-valid {
  border-radius: 25px;
  height: 100px;
  margin: 10px;
  -webkit-filter: drop-shadow(0px 0px 8px #eb0606);
  box-shadow: 0px 0px 25px 10px #ffb3b3;
}
.deck-injury-valid:hover {
  border-radius: 25px;
  height: 100px;
  margin: 10px;
  -webkit-filter: drop-shadow(0px 0px 8px #eb0606);
  box-shadow: 0px 0px 35px 15px #ffb7b7;
}
.players-in-game {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  margin-bottom: 45px;
  margin-top: -30px;
  height: 27vh;
}
.player-valid {
  -webkit-filter: drop-shadow(0px 0px 8px #eb0606);
  box-shadow: 0px 0px 25px 10px #ffb3b3;
}



